.toggle {
  padding: 10px 10px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible; }
  .toggle:hover {
    opacity: 0.7; }
  .toggle.toggle--active:hover {
    opacity: 0.7; }
  .toggle.toggle--active .toggle-inner,
  .toggle.toggle--active .toggle-inner::before,
  .toggle.toggle--active .toggle-inner::after {
    background-color: #cccccc; }

.toggle-box {
  width: 25px;
  height: 19px;
  display: inline-block;
  position: relative; }

.toggle-inner {
  display: block;
  top: 50%;
  margin-top: -1.5px; }
  .toggle-inner, .toggle-inner::before, .toggle-inner::after {
    width: 25px;
    height: 3px;
    background-color: #cccccc;
    border-radius: 0;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  .toggle-inner::before, .toggle-inner::after {
    content: "";
    display: block; }
  .toggle-inner::before {
    top: -8px; }
  .toggle-inner::after {
    bottom: -8px; }

.toggle--animation .toggle-inner {
  transition-duration: 0.22s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }
  .toggle--animation .toggle-inner::before {
    transition: top 0.1s 0.25s ease-in, opacity 0.1s ease-in; }
  .toggle--animation .toggle-inner::after {
    transition: bottom 0.1s 0.25s ease-in, transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19); }

.toggle--animation.toggle--active .toggle-inner {
  transform: rotate(225deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  .toggle--animation.toggle--active .toggle-inner::before {
    top: 0;
    opacity: 0;
    transition: top 0.1s ease-out, opacity 0.1s 0.12s ease-out; }
  .toggle--animation.toggle--active .toggle-inner::after {
    bottom: 0;
    transform: rotate(-90deg);
    transition: bottom 0.1s ease-out, transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1); }

